.icon-info {
  color: inherit;
  padding: 15px;
}

#game-field {
  justify-content: center;
  align-items: center;
  display: flex;
}

.game {
  border-right: 2px solid var(--color-black);
  border-bottom: 2px solid var(--color-black);
  grid-template-rows: repeat(3, 20vmin);
  grid-template-columns: repeat(3, 20vmin);
  display: grid;
}

.game__cell {
  text-align: center;
  border-top: 2px solid var(--color-black);
  border-left: 2px solid var(--color-black);
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-size: 18vmin;
  line-height: 1;
  display: flex;
}

.notification {
  background-color: var(--color-white);
  box-shadow: var(--box-shadow);
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: clamp(300px, 30%, 500px);
  padding: 30px 20px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.notification__text {
  text-transform: uppercase;
  margin: 0 0 20px;
  font-size: 30px;
  font-weight: 700;
}

.notification__question {
  font-variant: small-caps;
  text-transform: lowercase;
  margin: 0 0 20px;
  font-size: 17px;
}

.notification__buttons {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  display: flex;
}

.notification__button {
  border: 1px solid var(--color-black);
  flex: 1;
  padding: 10px;
}

:root {
  --color-black: #000;
  --color-white: #fff;
  --box-shadow: 0 0 20px 1px #0000004d;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  color: var(--color-black);
  grid-template-rows: 1fr auto;
  height: 100vh;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  font-weight: 400;
  display: grid;
}

button {
  cursor: pointer;
  font: inherit;
  color: inherit;
  background-color: #0000;
  border: none;
  padding: 0;
}
